a{
  text-decoration: none;
  color: inherit;
  margin: 0 auto;
}
.button {
  width: 200px;
  border-radius: 50px;
  border: 1px solid white;
  outline: none;
  margin: 20px auto;
  transition: all .4s;
  cursor: pointer;
  font-family: inherit;

  &:hover {
    border: 1px solid white;
    background: #000;
    color: white;
    transition: all .4s;
  }

  &:active {
    border: 1px solid rgba(26, 80, 107, 0.75);
    background: #000;
    color: white;
    transition: all .4s;
  }
  &:focus {
    border: 1px solid white;
    background: #000;
    color: white;
    transition: all .4s;
  }

}